@import "https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap";
:root {
  --grid-cols-desktop: 12;
  --grid-cols-tablet: 6;
  --grid-cols-mobile: 2;
  --bg: #fff;
  --ink: #111;
  --ink-soft: #6b6b6b;
  --muted: #f2f2f2;
  --divider: #e6e6e6;
  --accent: #b10f17;
  --accent-dark: #8f0c12;
  --h1: clamp(34px, 5vw, 64px);
  --h2: clamp(22px, 3vw, 28px);
  --h3: clamp(14px, 2vw, 16px);
  --lead: 18px;
  --text: 15px;
  --small: 13px;
  --ease: .2s ease;
  --page-w: 1260px;
  --side-pad: 120px;
  --col: 63px;
  --gut: 24px;
  --grid-w: 1020px;
  --menu-bg-1: #b10f17;
  --menu-bg-2: #c2262d;
  --navpanel-h: 700px;
}

.grid {
  max-width: var(--grid-w);
  row-gap: var(--gut);
  column-gap: var(--gut);
  grid-template-columns: repeat(var(--grid-cols-mobile), minmax(0, 1fr));
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

@media (width >= 744px) {
  .grid {
    grid-template-columns: repeat(var(--grid-cols-tablet), minmax(0, 1fr));
  }
}

@media (width >= 1200px) {
  .grid {
    grid-template-columns: repeat(var(--grid-cols-desktop), var(--col));
  }
}

*, :before, :after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Cinzel, serif;
  font-weight: 700;
  font-size: var(--text);
  color: var(--ink);
  background: var(--bg);
  margin: 0;
  line-height: 1.5;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  display: block;
}

img:hover {
  transition: transform var(--ease);
  transform: scale(1.03);
}

.container {
  max-width: var(--page-w);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: max(16px, min(4vw, 24px));
  padding-right: max(16px, min(4vw, 24px));
}

@media (width >= 1200px) {
  .container {
    padding-inline: var(--side-pad);
  }
}

.grid-2 {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 16px;
  display: grid;
}

@media (width >= 744px) {
  .grid-6 {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 20px;
    display: grid;
  }
}

@media (width >= 1200px) {
  .grid-12 {
    grid-template-columns: repeat(12, var(--col));
    column-gap: var(--gut);
    justify-content: center;
    display: grid;
  }
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.navpanel {
  height: min(var(--navpanel-h), 100vh);
  z-index: 9999;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: opacity var(--ease), transform var(--ease), visibility var(--ease);
  background: linear-gradient(90deg, color-mix(in srgb, var(--menu-bg-1) 96%, transparent), var(--menu-bg-2));
  color: #fff;
  scrollbar-width: none;
  --np-link-fs: 18px;
  --np-link-lh: 20px;
  --np-link-ls: 1.6px;
  --np-label-fs: 14px;
  --np-label-lh: 22px;
  --np-cta-fs: 11px;
  --np-cta-lh: 14px;
  --np-cta-ls: 1.8px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -8px);
}

.navpanel.is-open {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
  transform: translate(-50%);
}

.navpanel__close {
  color: #fff;
  cursor: pointer;
  transition: opacity var(--ease);
  z-index: 10001;
  background: none;
  border: 0;
  width: 32px;
  height: 32px;
  margin-left: auto;
  font-size: 28px;
  line-height: 1;
  display: block;
  position: sticky;
  top: 0;
}

.navpanel__close:before {
  content: "";
  background: var(--menu-bg-1);
  opacity: .6;
  z-index: -1;
  border-bottom-left-radius: 6px;
  position: absolute;
  inset: 0;
}

.navpanel__close:hover, .navpanel__close:focus-visible {
  opacity: .85;
}

.navpanel__content {
  row-gap: 32px;
  width: 100%;
  max-width: 1260px;
  margin-left: 0;
  margin-right: auto;
  padding: 32px 16px 40px;
  display: grid;
  position: relative;
}

.navpanel__logo img {
  object-fit: contain;
  width: 87px;
  height: 65px;
  display: block;
}

.navpanel__nav {
  gap: 32px;
  margin-top: 8px;
  display: grid;
}

.navpanel__nav a {
  text-transform: uppercase;
  color: #fff;
  font-size: var(--np-link-fs);
  line-height: var(--np-link-lh);
  letter-spacing: var(--np-link-ls);
  text-decoration: none;
  transition: color .3s;
}

.navpanel__nav a:hover, .navpanel__nav a:focus-visible {
  text-decoration: underline;
}

.navpanel__contacts {
  gap: 16px;
  margin-top: 4px;
  display: grid;
}

.navpanel__row {
  gap: 8px;
  display: grid;
}

.navpanel__row--mt16 {
  gap: 8px;
  margin-top: 16px;
}

.navpanel__label, .navpanel__phone {
  text-transform: uppercase;
  color: #fff;
  font-size: var(--np-label-fs);
  line-height: var(--np-label-lh);
}

.navpanel__phone {
  transition: color var(--ease);
  text-decoration: none;
}

.navpanel__phone:hover, .navpanel__phone:focus-visible {
  color: #ffdede;
}

.navpanel__cta {
  font-size: var(--np-cta-fs);
  line-height: var(--np-cta-lh);
  letter-spacing: var(--np-cta-ls);
  text-transform: uppercase;
  color: #fff;
  transition: opacity var(--ease);
  width: 126px;
  margin-top: 8px;
  padding-bottom: 6px;
  text-decoration: none;
  display: inline-block;
}

.navpanel__cta:hover, .navpanel__cta:focus-visible {
  opacity: .85;
}

:root {
  --navpanel-h: 568px;
}

@media (width >= 744px) {
  :root {
    --navpanel-h: 842px;
  }

  .navpanel {
    --np-link-fs: 20px;
    --np-link-lh: 22px;
    --np-link-ls: 1.8px;
    --np-label-fs: 15px;
    --np-label-lh: 24px;
    --np-cta-fs: 12px;
    --np-cta-lh: 16px;
    --np-cta-ls: 2px;
  }

  .navpanel__content {
    margin-left: 121px;
    padding: 32px max(16px, min(4vw, 24px)) 40px;
  }
}

@media (width >= 1200px) {
  :root {
    --navpanel-h: 700px;
  }

  .navpanel {
    --np-link-fs: 22px;
    --np-link-lh: 22px;
    --np-link-ls: 2px;
    --np-label-fs: 16px;
    --np-label-lh: 27px;
    --np-cta-fs: 12px;
    --np-cta-lh: 16px;
    --np-cta-ls: 2px;
  }
}

.header {
  color: #fff;
  background: url("gallery-main.a74fc6a9.png") center / cover no-repeat;
  width: 100%;
  height: max(520px, min(80vh, 840px));
  margin: 0 auto;
  position: relative;
}

.header:after {
  content: "";
  pointer-events: none;
  background: linear-gradient(#0000008c, #00000059 40%, #0000008c);
  position: absolute;
  inset: 0;
}

.header__inner.container {
  grid-column: 1 / -1;
}

.header__inner {
  z-index: 3;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 56px;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  top: 24px;
}

.header__logo {
  margin: 0;
}

.header__logo-img {
  object-fit: contain;
  width: auto;
  height: 56px;
  display: block;
}

.header__actions {
  justify-content: flex-end;
  align-items: center;
  gap: 24px;
  margin-left: auto;
  display: -ms-flexbox;
  display: flex;
}

.header__icon {
  cursor: pointer;
  transition: transform var(--ease);
  background: none;
  border: 0;
  place-items: center;
  width: 24px;
  height: 24px;
  padding: 0;
  line-height: 1;
  display: grid;
}

.header__icon > img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  display: block;
}

.header__icon:hover, .header__icon:focus-visible {
  transform: translateY(-1px);
}

.header__title {
  padding-top: var(--hdr-title-pt, 200px);
  text-align: center;
  z-index: 2;
  justify-content: center;
  align-items: flex-start;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  inset: 0;
}

.header__title h1 {
  font-size: var(--h1);
  letter-spacing: .08em;
  text-shadow: 0 2px 18px #0000008c;
  margin: 0;
  font-weight: 700;
  line-height: 1.05;
}

.header__phone {
  position: relative;
}

.header__contacts {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity var(--ease), transform var(--ease), visibility var(--ease);
  color: #fff;
  text-align: right;
  backdrop-filter: blur(2px);
  z-index: 5;
  background: #00000047;
  border-radius: 6px;
  gap: 8px;
  width: 260px;
  padding: 10px 12px;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  display: grid;
  position: absolute;
  top: 50%;
  right: calc(100% + 12px);
  transform: translateY(-50%)translateX(8px);
}

.header__contact-block {
  -ms-flex-direction: column;
  flex-direction: column;
  display: -ms-flexbox;
  display: flex;
}

.header__phone:hover .header__contacts, .header__phone:focus-within .header__contacts {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateY(-50%)translateX(0);
}

.header__contact-title {
  margin: 0;
}

.header__contact-phone {
  color: #fff;
  transition: opacity var(--ease);
  text-decoration: none;
}

.header__contact-phone:hover, .header__contact-phone:focus-visible {
  opacity: .8;
}

@media (width >= 744px) {
  .header__inner {
    height: 60px;
    top: 40px;
  }

  .header__logo-img {
    height: 60px;
  }

  .header__actions {
    gap: 55px;
  }

  .header__title {
    --hdr-title-pt: 256px;
  }

  .header__icon {
    width: 32px;
    height: 32px;
  }
}

@media (width >= 1200px) {
  .header__inner {
    height: 65px;
    top: 50px;
  }

  .header__logo-img {
    height: 65px;
  }

  .header__title {
    --hdr-title-pt: 198px;
  }
}

.hours {
  margin-top: 100px;
}

.hours__grid {
  max-width: var(--grid-w);
