  column-gap: var(--gut);
  grid-template-columns: repeat(var(--grid-cols-mobile), minmax(0, 1fr));
  align-items: start;
  column-gap: var(--gut);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

@media (width >= 744px) {
  .hours__grid {
    grid-template-columns: repeat(var(--grid-cols-tablet), minmax(0, 1fr));
  }
}

@media (width >= 1200px) {
